<template>
<section class="wrap has_header_wrap c_wrap">
	<section class='wrap_in'>
		<!-- 头部标题 -->
		<mt-header fixed title="订单详情">
	      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
		</mt-header>
		<!-- 头部标题 end -->
		
		<!-- 详情内容 -->
		<section :class="{hpb: isShowBot}">
			<!-- 订单状态 -->
			<div class="order_state">
				<div>订单状态：{{ curOrderList.orderStatus | orderStatusFilter }}</div>
				<div>
					<!-- 待付款 -->
					<!-- <mt-button 
						v-if="curOrderList.orderStatus == 1"
						type="default" size="small" plain>取消订单</mt-button> -->
		      		<mt-button 
						v-if="curOrderList.orderStatus == 1"
		      			type="primary" size="small">支付</mt-button>
		      		<!-- 代付款 end -->
		      		<!-- 待发货、已完成、已取消 --><!-- 待发货、已完成、已取消 end -->
		      		<!-- 待收货 -->
		      		<mt-button 
						v-if="curOrderList.orderStatus == 4"
		      			type="primary" 
		      			size="small"
		      			@click="takeDelivery(curOrderList)">确认收货</mt-button>
		      		<!-- 待收货 end -->
				</div>
			</div>
			<!-- 订单状态 -->
			<div class="infos">
				<p>订单编号：<span>{{ curOrderList.orderCode }}</span></p>
				<p>下单时间：<span>{{ curOrderList.orderDate }}</span></p>
			</div>
			<div class="infos">
				<p>收货人：<span>{{ curOrderList.contactMan }}</span></p>
				<p class="address">收货地址：<span><!-- {{ orderInfo.city }}{{ orderInfo.deliveryAddress }} -->{{ curOrderList.deliveryAddress }}</span></p>
			</div>
			<div class="order_list">
				<div class="item_list d_flex" v-for="(od,index) in curOrderList.orderDetails" @click="jumpDetail(od)">
		      		<div class="list_img">
		      			<img v-lazy="od.productUrl" :alt="od.productName">
		      		</div>
		      		<div class="list_info">
		      			<div class="li_top d_flex">
		      				<h3>{{ od.productName }}</h3>
		      				<p>{{ od.amt ? '￥'+od.amt:'' }}</p>
		      			</div>
		      			<div class="li_bottom d_flex">
	      					<span>{{ od.productProperty }}</span>
	      					<span>{{ od.qty ? 'X'+od.qty:'' }}</span>
		      			</div>
		      		</div>
		      	</div>
			</div>
			<!-- 支付方式 待付款、已取消没有 -->
			<div class="pay_ways">
				<div class="way">
					<span>支付方式：</span><span>{{ curOrderList.payMode?curOrderList.payMode.modeName:'' }}</span>
				</div>
				<div class="way_info d_flex">
					<div>
						<p>配送方式：<span>{{ curOrderList.logistics?curOrderList.logistics.logisticsName:'' }}</span></p>
						<p>快递单号：<span>{{ curOrderList.logisticsNumber }}</span></p>
					</div>
					<div>
						<mt-button 
							type="default" 
							size="small" 
							plain
							@click="jumpExpress(curOrderList)">查看物流</mt-button>
					</div>
				</div>
			</div>
			<!-- 支付方式 待付款、已取消没有 end -->
			<div class="counts">
				<p class="d_flex"><span>商品总额： </span><span><!-- {{orderInfo.needAmt}} -->{{ curOrderList.needAmt||curOrderList.needAmt==0?'￥'+curOrderList.needAmt:'' }}</span></p>
				<p class="d_flex"><span>运费： </span><span>+ ￥{{ curOrderList.chargeDiscount }}  </span></p>
				<p class="d_flex"><span>促销立减： </span><span>- ￥<!-- {{ curOrderList.productDiscount }} -->{{ curOrderList.manualDeduction }}</span></p>
				<p class="d_flex"><span>运费减免： </span><span>- ￥{{ curOrderList.chargeDiscount }}</span></p>
				<p class="d_flex"><span>折扣小计： </span><span>- ￥0.00</span></p>
				<div class="all_count">
					<p>实付金额：<i class="r_color">{{ curOrderList.amtDiscount||curOrderList.amtDiscount==0?'￥'+curOrderList.amtDiscount:'' }}</i></p>
				</div>
				<div class="count_btns d_flex">
					<a href="http://vipwebchat.tq.cn/pageinfo.jsp?version=vip&admiuin=9815275&ltype=1&iscallback=0&page_templete_id=122543&is_message_sms=0&is_send_mail=0&action=acd&acd=1&type_code=123"><span>在线客服</span></a>
					<a href="tel:400-9309-990"><span>客服电话</span></a>
				</div>
			</div>
			<!-- 您可能会关心 -->
			<!-- 暂时隐藏
			<div class="recom">
				<div class="r_title">
					<h3>您可能会关心</h3>
				</div>
				<ul class="recom_list d_flex">
					<li>
						<i></i>
						<img src="../../../static/images/proimg.jpg" alt="">
						<h3>赛典 单唾液酸四己糖神四己糖神四己糖神</h3>
						<span>2ml:20mg*6支/盒</span>
						<h3 class="r_color">￥420.00</h3>
					</li>
					<li>
						<img src="../../../static/images/proimg.jpg" alt="">
						<h3>赛典 单唾液酸四己糖神四己糖神四己糖神</h3>
						<span>2ml:20mg*6支/盒</span>
						<h3 class="r_color">￥420.00</h3>
					</li>
					<li>
						<img src="../../../static/images/proimg.jpg" alt="">
						<h3>赛典 单唾液酸四己糖神四己糖神四己糖神</h3>
						<span>2ml:20mg*6支/盒</span>
						<h3 class="r_color">￥420.00</h3>
					</li>
					<li>
						<img src="../../../static/images/proimg.jpg" alt="">
						<h3>赛典 单唾液酸四己糖神四己糖神四己糖神</h3>
						<span>2ml:20mg*6支/盒</span>
						<h3 class="r_color">￥420.00</h3>
					</li>
				</ul>
			</div> -->
			<!-- 您可能会关心 end -->
		</section>

		<!-- 底部操作按钮 -->
		<!-- <div class="bot_btns" v-show="curOrderList.orderStatus | isShowBot"> -->
		<div class="bot_btns" v-show="isShowBot">
			<mt-button 
				class="cancel"
				type="default"
				@click="removeOrder(curOrderList)">取消</mt-button>
		</div>
		<!-- 底部操作按钮 end -->
		<!-- 详情内容 end -->
	
	</section>
</section>	
</template>

<script>
import { Header, Button, Toast, MessageBox } from 'mint-ui';

export default {
	name: 'order_detail',
	data (){
		return {
			flag: false,
			orderId: this.$route.query.id, //获取订单状态
			orderInfo: '',//订单信息
			curOrderList: this.$store.state.orderInfo?JSON.parse(this.$store.state.orderInfo):'',//当前订单信息
		}
	},
	// props: ['curOrderList'],
	filters: {
		//订单状态过滤
		orderStatusFilter ( data ){

			// 1.在线未支付    2.货到付款待确认    3.待发货    4.待收货    5.物流待收款    6.财务待收款    7.已完成    8.已取消    9.支付中
			if( data == 1 || data == 9 ){ return '待付款' }
			else if( data == 3 || data == 2 ){ return '待发货' }
			else if( data == 4 ){ return '待收货' }
			else if( data == 5 || data == 6 || data == 7 ){ return '已完成' }
			else if( data == 5 || data == 6 ){ return '已收货' }
			else if( data == 8 ){ return '已取消' }
		},
	},
	computed: {
		//是否显示底部取消
		isShowBot (){
			const data = this.curOrderList.orderStatus;
			// console.log('data: ', data)
			if(!data){return false}
				// console.log(data)
			if( data == 4 || data == 5 || data == 6 || data == 7 || data == 8 ){
				return false;
			}else{
				return true;
			}
		},
	},
	components: {
		'mt-header': Header,
		'mt-button': Button,
	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	created (){
		const _self = this;
	},
	methods: {
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},
		//跳转查看物流
		jumpExpress (){
			// this.$router.push({ path: '/myinfo/order/express', query: { orderId: '007' } });
	    	this.$router.push({ path: '/order/express'});
		},
		//跳转商品详情
		jumpDetail ( data ){
			// console.log(data)
			// this.$router.push({path: '/product_detail', query: {proid: data.productID}});
			this.$router.replace({path: '/product_detail', query: {proid: data.productID}});
		},
		//取消订单
		removeOrder ( data ){
			// console.log(data)
			const _self = this;

			if(this.flag){return}
			MessageBox.confirm('确定执行此操作?').then(action => {
				_self.flag = true;	
				_self.$ajax({
					url: _self.webRoot + '/order/updateOrderStatusByCancel',
					data: {
						orderId: data.orderID
					}	
				})
				.then(function(res){
					// console.log(res)
					_self.$options.methods.showToast( res.data.msg );
					_self.flag = false;
					if( res.data.success ){
						data.orderStatus = 8;//改订单状态为已取消
						_self.$emit('cUpdata', data);
						setTimeout(function(){
							_self.$router.replace({path: '/order'});
						},1000)
					}
				})
				.catch(function(err){
					console.log(err)
					_self.flag = false;
				})
			});	
		},
	    //确认收货
	    takeDelivery ( item ){
	    	const _self = this;

	    	if( this.flag ){return false}
    		MessageBox.confirm('确定执行此操作?').then(action => {
		    	_self.flag = true;	
		    	_self.$ajax({
		    		url: _self.webRoot + '/order/updateOrderStatusByConfirmReceipt',
		    		data: {
		    			orderId: item.orderID
		    		}
		    	})
		    	.then(function(res){
		    		// console.log(res)
		    		if( !res.data.success ){
		    			_self.$options.methods.showToast( res.data.msg );
		    			_self.flag = false;
		    			return false;
		    		}
					_self.$options.methods.showToast( res.data.msg );
					setTimeout(function(){
						_self.$router.replace({path: '/order'});
						_self.flag = false;
					},1000);

		    	})
		    	.catch(function(err){
		    		console.log(err)
		    	})
	    	}).catch(function(cancel){

	    	});
	    },

		//公共函数
		// 后退
		goBack (){	
			this.$router.go(-1);
		},
		//显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	},
}	
</script>

<style scoped>
	@import '../../../static/css/headerTitle.css';	
	@import './Css/OrderDetail.css';	
</style>
<style>
.order_state div:nth-of-type(2) .is-plain .mint-button-text {
	color: #fff;
}
</style>


